.App {
  text-align: center;
  padding: 5rem;
  background-color: rgb(0, 0, 0);
}

.recipe-box {
  padding: 2.5rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 2rem;
  color: white;
  margin-top: 3rem;
}

.Recipe {
  background-color: rgb(29, 29, 29);
  padding: 1rem;
  border-radius: 5px;

  h2 {
    font-size: 1.5rem;
    margin-bottom: .5rem;
  }

  p {
    color: rgb(212, 212, 212);
  }

  &__imgbox {
    height: 15rem;
    overflow: hidden;
    border-radius: 5px;

    img {
      width: 100%;
    }
  }
}

.search-bar {
  padding: .3rem;
  transition: all .3s ease;

  &:focus {
    outline: none;
    width: 20rem;
  }
}

.search-button {
  margin: 1rem;
  background-color: green;
  color: white;
  padding: .3rem;
  border-radius: 2px;
  border: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}